<template>
    <div>
        <table>
            <tr>
                <th colspan="4">{{curQuestion.btitle}}</th>
            </tr>
            <tr>
                <td>{{curQuestion.bnum}}</td>
                <td>{{curQuestion.userid}}</td>
                <td>{{curQuestion.breadcnt}}</td>
                <td>{{curQuestion.bwritedate}}</td>
            </tr>
            <tr>
                <td colspan="4">{{curQuestion.bcontent}}</td>
            </tr>
        </table>
        <div>
            <input type="button" @click='gotoList' value="목록">
            <input type="button" @click='gotoModify' value="수정">
            <input type="button" @click='gotoDelete' value="삭제">
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            curQuestion:{
                bnum : '',
                btitle : '',
                userid : '',
                breadcnt : '',
                bwritedate : '',
                bcontent : '',
                questionnum : '',
            }
        }
    },
    created() {
        this.curQuestion.bnum = this.$route.params.bnum;
        axios.get("http://localhost:7777/happyhouse/qna/read/" + this.curQuestion.bnum)
        .then((response) => {
            this.curQuestion = response.data;
        })
        .catch((ex) => {console.log(ex)});

        console.log(this.curQuestion);
    }, 
    methods: {
        gotoModify(){
            this.$router.push({name : 'Modify', params : {question : this.curQuestion}});
        },
        gotoList(){
            this.$router.push("/qna/list");
        },
        gotoDelete(){
            if(confirm("삭제하시겠습니까?")){
                axios.delete("http://localhost:7777/happyhouse/qna/delete/" + this.curQuestion.bnum)
                .then(() => {
                    this.$router.push("/qna/list");
                })
                .catch((ex) => {console.log(ex)});
            }
        },
        addComment(){
        }
    }
}
</script>