import Vue from "vue";
import VueRouter from "vue-router";
import QnA from "../views/QnA.vue";
import QnaList from "@/components/QnAList.vue";
import QnaRegister from "@/components/QnARegister.vue";
import QnaModify from "@/components/QnAModify.vue";
import QnaRead from "@/components/QnARead.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/qna",
    name: "QnA",
    component: QnA,
    redirect: "/qna/list",
    children: [
      {
        path: "/qna/list",
        name: "List",
        component: QnaList,
      },
      {
        path: "/qna/register",
        name: "Register",
        component: QnaRegister
      },
      {
        path: "/qna/modify",
        name: "Modify",
        component: QnaModify
      },
      {
        path: "/qna/list/:bnum",
        name: "Read",
        component: QnaRead
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
