<template>
  <div>
    <input type="text" v-model="keyword.word" @keyup.enter="search"/>
    <button @click='search'>검색</button>
    <table>
      <tr>
        <th>번호</th>
        <th>제목</th>
        <th>조회수</th>
        <th>작성날짜</th>
      </tr>
      <tr v-for="qna in qnas" :key="qna.bnum">
        <td>{{qna.bnum}}</td>
        <td><router-link :to="{name: 'Read', params: { bnum: qna.bnum } }">{{qna.btitle}}</router-link></td>
        <td>{{qna.breadcnt}}</td>
        <td>{{qna.bwritedate}}</td>
      </tr>
    </table>
    <input type="button" @click='gotoRegister' value="질문 하기">
  </div>
</template>

<script>
import axios from "axios";

export default {
  data(){
    return {
      qnas:[],
      keyword : {
        word : '',
        type : 'bcontent'
      }
    }
  },
  created() {
    axios.get("http://localhost:7777/happyhouse/qna/list")
    .then((response) => {
      console.log(response);
      this.qnas = response.data;
    })
    .catch((ex) => {
      console.log(ex);
    })
  },
  methods: {
    search(){
      axios.post("http://localhost:7777/happyhouse/qna/search", this.keyword)
      .then((response)=>{
        this.qnas = response.data;
      })
      .catch((ex) =>{
        console.log(ex);
      })
    },
    gotoRegister(){
      this.$router.push("/qna/register");
    }
  }
};
</script>